import random from "@/js/random.js"
import store from '@/store'

function Food(type) {
  this.type = type
}


function JumpFood(positoin = { right: undefined, top: undefined }) {
  this.position = positoin
};
JumpFood.prototype = {
  ...new Food(0),
  constructor: JumpFood,
  run(snakes, foods, snake) {
    // console.log(snakes, foods, snake)
    snake.eat(this)
    snake.move(this.position)
    this.jump(snakes, foods);
  },
  jump(snakes, foods) {
    do { var allow_position = { right: random(store.state.ui_setting.size), top: random(store.state.ui_setting.size) } }
    while (snakes.some((snake) => { return snake.body.some((section) => { return (section.right == allow_position.right && section.top == allow_position.top) }) })
      || foods.some((food) => { return (food.right == allow_position.right && food.top == allow_position.top) }))
    this.position = allow_position
  }
}


function StaticFood(positoin = { right: undefined, top: undefined }) {
  this.position = positoin
};
StaticFood.prototype = {
  ...new Food(1), constructor: StaticFood, 
  run: function (snakes, foods, snake) {
    foods.splice(foods.indexOf(this), 1)
    snake.eat(this)
    snake.move(this.position)
  }
}

function DirectionFood(positoin = { right: undefined, top: undefined }) {
  this.position = positoin
  this.direction = parseInt(Math.random() * 4)
}
DirectionFood.prototype = {
  ...new Food(2), constructor: DirectionFood, run: function (snakes, foods, snake) {
    if (snake.direction == this.direction) {

    }
  }
}

function PauseFood(positoin = { right: undefined, top: undefined }) {
  this.position = positoin
}


function TeleportFood(positoin = { right: undefined, top: undefined }) {
  this.position = positoin
}


export default [JumpFood, StaticFood]