      <el-progress
        :percentage="snake.energy * 10"
        :color="customColors"
        :show-text="false"
        :stroke-width="8"
      ></el-progress>
    </div>
  </div>
</template>

<script>
import avatar from "js/avatar.js";
export default {
  data() {
