import Food from './food.js'

function Greedysnake(setting,id) {
    this.body = [
        { right: 5, top: setting.init_top },
        { right: 4, top: setting.init_top },
        { right: 3, top: setting.init_top },
        { right: 2, top: setting.init_top },
    ];
    this.modify_direction = true;
    this.direction = 2;
    this.key_control = setting.key_ref
    this.name = setting.name
    this.id = id
    this.interval = null
    this.time = 0
    this.energy = 10
    this.time_per_distance = 30
    this.onspeed = false
    this.speeding = false
}
//前进，考虑所有情况
Greedysnake.prototype.run = function (snakes, foods, range, lost,struck) {
    if(this.speeding){
    this.energy<=0 || this.energy--
    }else{
    this.energy>=10 || this.energy++
    }


    let next_position
    switch (this.direction) {
        case 1:
            next_position = { right: this.body[0].right, top: this.body[0].top - 1 }
            break;
        case 3:
            next_position = { right: this.body[0].right, top: this.body[0].top + 1 }
            break;
        case 2:
            next_position = { right: this.body[0].right + 1, top: this.body[0].top }
            break;
        case 0:
            next_position = { right: this.body[0].right - 1, top: this.body[0].top }
            break;
    }

    if (next_position.right >= range || next_position.right < 0 || next_position.top >= range || next_position.top < 0) {
        //撞墙如何处理

        //穿越
        next_position.right = (next_position.right + range) % range
        next_position.top = (next_position.top + range) % range

        //弹框输了
        // lost(snakes.indexOf(this)+1)
        // return
    }

    let strike = 0
    for (let index in snakes) {
        for (let body_index = 0; body_index < snakes[index].body.length-1; body_index++) {
            if (snakes[index].body[body_index].right == next_position.right && snakes[index].body[body_index].top == next_position.top) {
                strike = index
                break
            }
        }
        if (strike) { break }
    }
    if (strike) {
        //撞到蛇身体如何

        //蛇变成豆
        struck(snakes.indexOf(this))


        //弹窗n号玩家输了
        // lost(snakes.indexOf(this) + 1);
        // this.move(next_position);
        return
    }

    let food;
    if(food = foods.find(function(food){return food.position.right == next_position.right&& food.position.top == next_position.top})){
        food.run(snakes, foods, this)
    }
    else{
        this.move(next_position)
    }

    // let eat = false
    // for (let index in foods) {
    //     if (foods[index].position.right == next_position.right && foods[index].position.top == next_position.top) {
    //         this.eat()
    //         eat = true
    //         this.move(next_position)
    //         foods[index].run(snakes, foods, this)
    //         break
    //     }
    // }
    // if (!eat) {
    //     this.move(next_position)
    // }

}
//只管移动，传入下次移动的位置
Greedysnake.prototype.move = function (position) {
    this.modify_direction = true

    for (let i = this.body.length - 1; i > 0; i--) {
        this.body[i] = this.body[i - 1];
    }

    this.body[0] = position
};
//负责改变方向和加速
Greedysnake.prototype.turn = function (keyCode) {
    if (this.modify_direction) {
        switch (keyCode) {
            case this.key_control[0]:
                if ([1, 3].includes(this.direction)) {
                    this.direction = 0;
                    this.modify_direction = false
                }
                 if(0 == this.direction){
                    this.onspeed = true
                }
                break;
            case this.key_control[1]:
                if ([2, 0].includes(this.direction)) {
                    this.direction = 1;
                    this.modify_direction = false
                }
                 if(1 == this.direction){
                    this.onspeed = true
                }
                break;
            case this.key_control[2]:
                if ([1, 3].includes(this.direction)) {
                    this.direction = 2;
                    this.modify_direction = false
                }
                 if(2 == this.direction){
                    this.onspeed = true
                }
                break;
            case this.key_control[3]:
                if ([2, 0].includes(this.direction)) {
                    this.direction = 3;
                    this.modify_direction = false
                }
                 if(3 == this.direction){
                    this.onspeed = true
                }
                break;
        }

    }
}
//负责减速
Greedysnake.prototype.decelerate  = function (keyCode) {
        switch (keyCode) {
            case this.key_control[0]:
                if(0 == this.direction){this.onspeed = false}
                break;
            case this.key_control[1]:
                if(1 == this.direction){this.onspeed = false}
                break;
            case this.key_control[2]:
                if(2 == this.direction){this.onspeed = false}
                break;
            case this.key_control[3]:
                if(3 == this.direction){this.onspeed = false}
                break;
        }
}

//只管吃豆，加长
Greedysnake.prototype.eat = function () {
    this.body.push({ right: this.body[this.body.length - 1].right, top: this.body[this.body.length - 1].top })
}



export default Greedysnake