export default function random_num(num){
    if(num.constructor == Number){return parseInt(Math.random()*num)}
    else if(num.constructor == Array){
        let probability = Math.random()
        let limit_bottom = 0
        for (let index in num){
            if(probability>=limit_bottom&&probability<limit_bottom+num[index][1]){
                return num[index][0]
            }
            limit_bottom+=num[index][1]
        } 
        return 0
    }
  }